import { Component, OnDestroy, OnInit, inject } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MenuItem } from 'primeng/api';
import { Usuario } from 'src/app/interfaces/login/login-respuesta.interface';
import { OrdenTrabajo } from 'src/app/interfaces/tarea.interface';
import { UsuarioService } from 'src/app/services/auth/usuario.service';
import { MessageService } from 'src/app/services/message.service';
import { PruebasService } from 'src/app/services/pruebas.service';
import { TareaService } from 'src/app/services/tarea.service';

@Component({
  selector: 'app-mostrar-orden-trabajo',
  templateUrl: './mostrar-orden-trabajo.component.html',
  styleUrls: ['./mostrar-orden-trabajo.component.scss']
})
export class MostrarOrdenTrabajoComponent implements OnInit, OnDestroy {

  private readonly tareaService = inject(TareaService);
  private readonly messageService = inject(MessageService);
  private readonly pruebasService = inject(PruebasService);
  private readonly activateRoute = inject(ActivatedRoute);

  ordenDeTrabajoID: string = null;
  ordenDeTrabajo: OrdenTrabajo = null;

  usuario = JSON.parse(localStorage.getItem('usuario'));

  ordenesTrabajo: OrdenTrabajo[];

  lineasToShow = [];
  lineasProcesoToShow = [];
  lineasFinalizadoToShow = [];

  otrasSubscripciones = [];

  ngOnInit(): void {
    this.activateRoute.params.subscribe({
      next: params => {
        this.ordenDeTrabajoID = params['id']
      }
    })

    this.otrasSubscripciones.push(
      this.tareaService.getOrdenTrabajo(this.ordenDeTrabajoID).subscribe(
        (response) => {
          console.log(response);
          this.ordenDeTrabajo = response;
          this.ordenDeTrabajo.lineasTareas.forEach((linea) => {
            this.lineasToShow.push({
              label: linea.descripcion,
              routerLink: `/tarea/${linea.id}`,
              disabled: linea.estado === "finalizado"
            });
            console.log(linea);
            console.log(`linea.estado (${linea.estado}) === "finalizado" => `, linea.estado === "finalizado");
          });
          console.log(this.lineasToShow);
        },
        (error) => {
          console.log(error);
          this.messageService.add({
            severity: 'error',
            summary: 'Error',
            detail: 'Hubo un error con las ordenes de Trabajo',
          });
        }
      )
    );
  }

  ngOnDestroy() {
    this.otrasSubscripciones.forEach((subscripcion) => {
      subscripcion.unsubscribe();
    });
  }
}
